import debounce from 'lodash/debounce';
import isNumber from 'lodash/isNumber';
import uniqueId from 'lodash/uniqueId';
import moment from 'moment';

const list = function (element, options, ReviewCollection, Pagination) {

  var $element = $(element);

  // HTML validator needs double quotes around data-* attributes,
  // and $.data() needs double quotes IN the JSON string to parse the data.
  // Therefore we use single quotes in the attribute, and convert before parsing
  // TODO: This might be a good candidate to move to an utility object, since it wil be used by many modules
  var config = $element.data('config');
  if (typeof config === 'string' && config !== '') {
    config = JSON.parse(config.replace(/'/g, '"'));
  }

  // Extend our config object with default options
  // TODO: Another candidate for moving into a central utlity object
  config = $.extend({
    "perpage": 5,
    "currentpage": 1
  }, config, options);

  // Document body. Used for scrolling upon page change.
  var $BODY = $('body');

  // Container for list filters
  var $containerFilters;

  // Container for list sorting
  var $containerSort;

  // Container for the list itself
  var $containerList;

  // Container for the list pagination
  var $containerPagination;

  // The review item elements
  var $reviewItems;

  // Our review collection instance. Data will be fetched from this thing.
  var reviewCollection = new ReviewCollection();

  // Pagination object instance.
  var reviewPagination;

  // Pagination: Items per page
  var perPage = config.perpage;

  // Pagination: Current page
  var currentPage = config.currentpage || 1;

  //scrollpostion of body
  var scrollPosition;

  // Total number of items
  var itemCount = 0;

  // Debounced list render function.
  // To prevent the list from constantly refreshing when either filters or sorting are abused.
  var renderListDebounced = debounce(renderList, 200);

  // Unique id to recognize this specific object instance when subcribers receive its signals
  var publisherId;

  function renderApp() {
    $element.html(generateLayoutTemplate());

    $containerFilters = $element.find('[data-role="review-filter"]');
    $containerFilters.on('change', 'input[type="checkbox"]', onFilterChange);

    // TODO: Categories are not available, data wil be available later
    // var filterData = {"options": reviewCollection.getCategories()};
    // if (filterData.options.length > 0) {
    // 	$containerFilters.html(templateFilters(filterData));
    // }

    $containerSort = $element.find('[data-role="review-sort"]');
    $containerSort.on('change', 'select', onSortChange);
    $containerSort.html(generateSelectTemplate());
    nn.initializeComponents($containerSort);

    $containerList = $element.find('[data-role="review-list"]');
    renderList();
    setPage(currentPage);

    $containerPagination = $element.find('[data-role="review-pagination"]');

    reviewPagination = new Pagination($containerPagination);
    reviewPagination.setItemCount(reviewCollection.getItems().length);
    reviewPagination.setPerPage(perPage);
    reviewPagination.setCurrentPage(currentPage);
    reviewPagination.render();

    var paginationPublisherId = reviewPagination.getPublisherId();
    scrollPosition = $BODY.scrollTop();

    $.subscribe('review:pagination:change', function (evt, data) {
      if (data.publisherId && data.publisherId === paginationPublisherId && isNumber(data.currentPage)) {
        setPage(data.currentPage);
        reviewPagination.setCurrentPage(data.currentPage);
        reviewPagination.render();
        $BODY.scrollTop(scrollPosition);
      }
    });

    $.publish('review:list:rendered', {
      "publisherId": getPublisherId()
    });
  }

  function onFilterChange() {
    var categories = [];
    $containerFilters.find('input[type="checkbox"]:checked').each(function (i, el) {
      categories.push($(el).val());
    });

    reviewCollection.filterByCategories(categories);
    renderListDebounced();
  }

  function onSortChange(evt) {
    switch ($(evt.target).val()) {
    case 'dateAsc':
      reviewCollection.sortBy('dtreviewed', true);
      break;
    case 'dateDesc':
      reviewCollection.sortBy('dtreviewed', false);
      break;
    case 'valueAsc':
      reviewCollection.sortBy('value', true);
      break;
    case 'valueDesc':
      reviewCollection.sortBy('value', false);
      break;
    default:
      reviewCollection.sortBy(null);
    }

    renderListDebounced();
  }

  function renderList() {
    var items = reviewCollection.getItems();
    itemCount = items.length;

    $containerList.html(generateListTemplate(items));
    $reviewItems = $containerList.find('.c-review');

    setPage(1);
  }

  function setPage(page) {

    page = parseInt(page);
    page = Math.max(page, 1);
    page = Math.min(page, (itemCount / perPage));

    if (page >= 1) {
      if (page === 1) {
        $reviewItems.slice(0, perPage).removeAttr('hidden');
        $reviewItems.slice(perPage).attr('hidden', 'hidden');
      } else {

        var offset = (page * perPage);
        $reviewItems.slice(0, offset).attr('hidden', 'hidden');
        $reviewItems.slice(offset, offset + perPage).removeAttr('hidden');
        $reviewItems.slice(offset + perPage).attr('hidden', 'hidden');
      }

      currentPage = page;
      if (typeof reviewPagination === 'object') {
        reviewPagination.setCurrentPage(currentPage);
      }
    }

  }

  // Check if we have a data URL, then kick off the list
  if (typeof config.src === 'string') {
    $.getJSON(config.src, {}, function (data) {
      reviewCollection.setData(data);
      // Sort by date by default
      reviewCollection.sortBy('dtreviewed', false);
      renderApp();
    });
  }

  function getPublisherId() {
    if (!publisherId) {
      publisherId = uniqueId('pubsubid');
    }
    return publisherId;
  }

  return {
    'getPublisherId': getPublisherId
  };
};

const generateLayoutTemplate = () => {
  return `
    <div class="u-margin-bottom--30">
      <div class="o-layout__row">
        <div class="o-layout__col-m-6" data-role="review-filter">
        </div>
        <div class="o-layout__col-m-6" data-role="review-sort">
        </div>
      </div>
    </div>
    <div data-role="review-list"></div>
    <div data-role="review-pagination"></div>`
}

const generateSelectTemplate = () => {
  return `<label class="c-form-label h3 u-block" for="">Sorteer op:</label>
    <div class="select size-full-select-field width-100">
      <select data-component="Select">
        <option value="dateDesc">Datum (nieuw-oud)</option>
        <option value="dateAsc">Datum (oud-nieuw)</option>
        <option value="valueDesc">Waardering (hoog-laag)</option>
        <option value="valueAsc">Waardering (laag-hoog)</option>
      </select>
    </div>`
}

const generateListTemplate = (items) => {
  return items.map(item => {
    return `
      <div class="c-review" itemprop="review" itemscope itemtype="http://schema.org/Review">
        <div class="o-layout__row">
          <div class="o-layout__col-m-3">
            <div class="c-review__profile">
              <p class="c-review__author" itemprop="author">${item.reviewer}</p>
              <div class="c-review__date">
                <meta itemprop="datePublished" content="${item.dtreviewed}">
                ${moment(item.dtreviewed).format('D MMMM YYYY')}
              </div>
            </div>
          </div>
          <div class="o-layout__col-m-9">
            <div class="c-review__content">
              <div itemprop="reviewBody">
              ${item.content.map(contentitem => contentitem.description)}
              </div>
              <div class="c-rating" itemprop="aggregateRating" itemscope itemtype="http://schema.org/AggregateRating">
                <meta itemprop="bestRating" content = "<%- item.best %>">
                <meta itemprop="worstRating" content = "1">
                <div class="c-rating__stars">
                  <div class="c-rating__stars__amount" style="width: ${Math.ceil((item.value / item.best) * 100)}"></div>
                </div>
                <p class="c-rating__totals" itemprop="ratingValue">${item.value}</p>

              </div>
            </div>
          </div>
        </div>
      </div>`;
  })
}

export default list;
