import compact from 'lodash/compact';
import lodashSortBy from 'lodash/sortBy';
import uniq from 'lodash/uniq';
import flatMap from 'lodash/flatMap';
import indexOf from 'lodash/indexOf';
import isArray from 'lodash/isArray';
import orderBy from 'lodash/orderBy';
import isNumber from 'lodash/isNumber';

const ReviewCollection = function () {
  var rawData,
    processedData,
    processed,
    categories,
    filterCategories,
    sort,
    perPage;

  /**
   * Get category from review
   * @private
   */
  function _getItemCategory(item) {
    return item.category;
  }

  /**
   * Set categories to available categories from our review collection
   * @private
   */
  function _setCategories() {
    categories = compact(lodashSortBy(uniq(flatMap(rawData.reviews, _getItemCategory))));
  }

  /**
   * ...
   * @private
   */
  function _itemCategoryInFilter(item) {
    return (indexOf(filterCategories, item.category) > -1);
  }


  /**
   * ...
   * @function
   */
  function setData(data) {
    rawData = data;
    processedData = rawData.reviews;
    processed = true;

    if (isArray(data.reviews) && data.reviews.length > 0) {
      _setCategories();
    }
  }


  /**
   * ...
   * @function
   */
  function getCategories() {
    return categories;
  }


  function filterByCategories(categories) {
    var filterCategoriesNew;

    if (isArray(categories) && categories.length > 0) {
      filterCategoriesNew = categories;
    } else {
      filterCategoriesNew = undefined;
    }

    if (filterCategoriesNew !== filterCategories) {
      processed = false;
    }

    filterCategories = filterCategoriesNew;
  }


  function processData() {

    processedData = rawData.reviews;

    //
    if (filterCategories) {
      processedData = processedData.filter(_itemCategoryInFilter);
    }

    //
    if (sort) {
      var sortKeys = [sort.key],
        sortOrders = [sort.ascend ? 'asc' : 'desc'];

      if (sortKeys !== ['dtreviewed']) {
        sortKeys.push('dtreviewed');
        sortOrders.push('desc');
      }

      processedData = orderBy(processedData, sortKeys, sortOrders);

    }

    //
    if (perPage) {
      var offset = 0,
        pages = Math.ceil(processedData.length / perPage),
        paginatedData = [];

      for (var i = 1; i <= pages; i++) {
        paginatedData.push(processedData.slice(offset, perPage));
        offset += perPage;
      }

    }

    processed = true;
  }

  function getItems() {
    if (!processed) {
      processData();
    }

    return processedData;
  }

  function setPerPage(_perPage) {
    if (!(isNumber(_perPage) && _perPage > 0)) {
      _perPage = null;
    }

    if (_perPage !== perPage) {
      processed = false;
    }

    perPage = _perPage;

  }


  function sortBy(key, ascend) {
    var sortNew;

    ascend = !!ascend;

    if (rawData.reviews[0].hasOwnProperty(key)) {
      sortNew = {
        "key": key,
        "ascend": ascend
      };
    } else {
      sortNew = undefined;
    }

    if (sort !== sortNew) {
      processed = false;
    }

    sort = sortNew;
  }

  // PUBLIC API
  return {
    "setData": setData,
    "filterByCategories": filterByCategories,
    "getCategories": getCategories,
    "getItems": getItems,
    "setPerPage": setPerPage,
    "sortBy": sortBy
  };
}

export default ReviewCollection;
