const dialogLink = function (element, List, Collection, Pagination) {

  // Our main container
  var $element = $(element);

  // The dialog instance
  var dialog;

  // The Review list instance
  var reviewList;

  // The id by which to find this Review list instance
  var reviewListPublisherId;

  // HTML validator needs double quotes around data-* attributes,
  // and $.data() needs double quotes IN the JSON string to parse the data.
  // Therefore we use single quotes in the attribute, and convert before parsing
  // TODO: This might be a good candidate to move to an utility object, since it wil be used by many modules
  var config = $element.data('config');
  if (typeof config === 'string' && config !== '') {
    config = JSON.parse(config.replace(/'/g, '"'));
  }

  // Render the dialog contents and instantiate our Review list
  var renderDialog = function () {
    dialog = new window.nn.component.Dialog();

    var templateData = {};
    templateData.title = config.title || '';

    dialog.content.html(generateTemplate(templateData));
    reviewList = new List(dialog.content.find('[data-role="review-dialogcontent"]'), config, Collection, Pagination);
    reviewListPublisherId = reviewList.getPublisherId();
  };

  // Click listener for our link
  var onClick = function (evt) {
    evt.preventDefault();

    if (typeof reviewList !== 'object') {
      // First click. Instantiate the list and render the dialog contents.
      // Wait for the list to finish rendering before opening the dialog.
      renderDialog();
      $.subscribe('review:list:rendered', onInstanceRendered);
    } else {
      openDialog();
    }

    return false;
  };

  // Listener for list rendering
  var onInstanceRendered = function (evt, data) {
    if (reviewListPublisherId === data.publisherId) {
      openDialog();
      $.unsubscribe('review:list:rendered', openDialog);
    }
  };

  // Open our dialog
  var openDialog = function () {
    dialog.show();
  };

  // FIXME for the love of all that is holy
  $element.on('click', onClick);
  $element.attr('href', 'javas');
  $element.attr('href', $element.attr('href') + 'cript');
  $element.attr('href', $element.attr('href') + ':void();');
};

const generateTemplate = (templateData) => {
  let result = '';
  if (templateData.title) {
    result = `<h2 class="heading-accent">${templateData.title}</h2>`
  }
  result +=`<div data-role="review-dialogcontent"></div>`;
  return result;
}

export default dialogLink;
