import debounce from 'lodash/debounce';
import range from 'lodash/range';
import uniqueId from 'lodash/uniqueId';
import isNumber from 'lodash/isNumber';

const Pagination = function(element) {
  var $CONTAINER = $(element);
  var GROUPLENGTH = 5;
  var MOBILEGROUPLENGTH = 3;

  var itemCount = 0,
    perPage = 5,
    currentPage = 1,
    config = $CONTAINER.data('config'),
    publisherId;


  if (typeof config === 'string' && config !== '') {
    config = JSON.parse(config.replace(/'/g, '"'));
  }

  function setItemCount(_itemCount) {
    itemCount = parseInt(_itemCount);
  }

  function setPerPage(_perPage) {
    perPage = parseInt(_perPage);
  }

  function setCurrentPage(_currentPage) {
    currentPage = parseInt(_currentPage);
  }

  function render() {
    var totalPages = Math.ceil(itemCount / perPage);
    var lastPage = totalPages - 1;
    var groupLength;

    if ($(window).width() < 479) {
      groupLength = MOBILEGROUPLENGTH;
    } else {
      groupLength = GROUPLENGTH;
    }

    currentPage = Math.min(currentPage, totalPages);
    currentPage = Math.max(currentPage, 1);

    var currentPagegroup = Math.ceil(currentPage / groupLength);
    var lastPagegroup = Math.ceil(totalPages / groupLength);
    var groupnumberStart = ((currentPagegroup - 1) * groupLength) + 1;
    var groupnumberEnd = groupnumberStart + groupLength;
    groupnumberEnd = Math.min(groupnumberEnd, totalPages);
    groupnumberEnd = Math.max(groupnumberEnd, 1);

    var pages = range(groupnumberStart, groupnumberEnd);

    $CONTAINER.html(generateTemplate({
      "pages": pages,
      "currentPage": currentPage,
      "lastPage": lastPage,
      "groupLength": groupLength,
      "currentPagegroup": currentPagegroup,
      "lastPagegroup": lastPagegroup
    }));
  }

  function getPublisherId() {
    if (!publisherId) {
      publisherId = uniqueId('pubsubid');
    }
    return publisherId;
  }


  function onClick(evt) {
    evt.preventDefault();
    var $target = $(evt.target);

    //FIXME ie hack for icon on our button
    if (!$target.attr('data-pagenumber')) {
      $target = $target.closest('[data-pagenumber]');
    }

    currentPage = parseInt($target.data('pagenumber'));

    if (currentPage && isNumber(currentPage)) {
      $.publish('review:pagination:change', {
        "publisherId": getPublisherId(),
        "currentPage": currentPage
      });
      render();
    }
  }

  $CONTAINER.on('click', onClick);
  // TODO attach to a generic resize module with debounce
  $(window).resize(debounce(render, 500));

  return {
    "setItemCount": setItemCount,
    "setPerPage": setPerPage,
    "setCurrentPage": setCurrentPage,
    "render": render,
    "getPublisherId": getPublisherId
  };
};

const renderLastItem = (data) => {
  let result = '';
  if (data.currentPagegroup < data.lastPagegroup ) {
    if (data.currentPage + data.groupLength > data.lastPage) {
      result += `<li class="c-pagination__item"><a href="#" data-pagenumber="${data.lastPage}" class="c-pagination__link" aria-label="volgende paginagroep">&hellip;</a></li>`;
    } else {
      result += `<li class="c-pagination__item"><a href="#" data-pagenumber="${data.currentPage + data.groupLength}" class="c-pagination__link" aria-label="volgende paginagroep">&hellip;</a></li>`;
    }
    result += `<li class="c-pagination__item"><a href="#" data-pagenumber="${data.lastPage}" class="c-pagination__link" aria-label="pagina ${data.lastPage}">${data.lastPage}</a></li>`;
  }
  if (data.currentPage < data.lastPage ) {
    result += `<li class="c-pagination__item c-pagination__button-next"><a href="#" data-pagenumber="${data.currentPage + 1}" class="c-button c-button--next" aria-label="volgende pagina"><span class="c-icon c-icon--angle-right c-button--next__icon" aria-hidden="true"></span></a></li>`;
  }
  return result;
}

const generateTemplate = (data) => {
  return `
    <div class="c-pagination">
      <ul class="c-pagination__list">
      ${(data.currentPage - 1 > 0) ? `<li class="c-pagination__item c-pagination__button-back"><a href="#" data-pagenumber="${data.currentPage - 1}" class="c-button c-button--back" aria-label="vorige pagina"><span class="c-icon c-icon--angle-left c-button--back__icon" aria-hidden="true"></span></a></a></li>` : ''}

      ${(data.currentPage - data.groupLength > 0) ? `
      <li class="c-pagination__item"><a href="#" data-pagenumber="1" class="c-pagination__link" aria-label="pagina 1">1</a></li>
      <li class="c-pagination__item"><a href="#" data-pagenumber="${data.currentPage - data.groupLength}" class="c-pagination__link" aria-label="vorige paginagroep">&hellip;</a></li>
      ` : ''}

      ${data.pages.map(page => `<li class="c-pagination__item"><a href="#" data-pagenumber="${page}" class="c-pagination__link ${(page=== data.currentPage) ? 'is-active' : ''}" aria-label="pagina ${page}">${page}</a></li>`)}

      ${renderLastItem(data)}
      </ul>
    </div>
  `
}

export default Pagination;
